<template>
   <div class="xiyi_list">
       <div class="nopage" v-if="orderList.length==0&&inited">
				<img class="nopage_img" src="../assets/images/index/noorder.png"/>
				<div class="nopage_desc"></div>
				<div class="btn_desc"></div>
				<div class="order_btn" @click="gotoPage()"></div>
		</div>

        <div v-if="orderList.length>0">
            <div class="list_body" v-for="(order,index) in orderList" :key="order.id" @click="gotoDetail(index)">
				<p class="borderline"></p>
				<div class="time_desc">: {{order.createDateStr}}</div>
				<div class="list_main">
					<img class="item_logo" :src="order.billLogo" alt="" />
					<div class="item_desc">: </div><div class="item_desc">{{order.orderNo}}</div>
					<div class="item_desc">: {{order.serviceDateStr}}</div>
					<div  class="item_desc highlight">{{order.statusStr}}</div>
				</div>
				<div class="status_bar">
					<span>: <span style="color: #ff8a00;">{{order.realAmount}}</span></span>
					<p class="fr btns">
						<span class="cancel" @click.stop="cancel(index)" v-if="order.status==1"></span>
						<span class="pay" @click.stop="pay(index)" v-if="order.status==1"></span>
					</p>
				</div>
			</div>
        </div>
   </div>
</template>

<script>
let vm;
import wx from 'weixin-js-sdk';
export default {
   data () {
       return {
          orderList:[
            //  {
                // id:'4',
                // createDateStr:'2019-4-19',
                // orderNo:'333',
                // serviceDateStr:'2019-3324',
                // statusStr:'432',
                // status:'1',
                // realAmount:'100',
                // billLogo:'http://img.e-shequ.com/Fu3XAhDbMYqnQiyyicsZfgQIGNdR',
            //  }
          ],
		  inited:false,
       };
   },
   created() {vm=this},
   mounted() {
          vm.common.checkRegisterStatus();
          let url = location.href.split('#')[0];
         vm.receiveData.wxconfig(vm,wx,['chooseWXPay','onMenuShareTimeline','onMenuShareAppMessage'],url);
        vm.query();//
   },
   methods: {
       query() {
            vm.receiveData.postData(vm,'/yunxiyi/bills/0',null,'res',function() {
                if(vm.res.success) {
                    vm.orderList=vm.res.result;
		            vm.inited = true;
                }else {
                    vm.inited = true;
                    alert("");    
                }
            })
       },
       //
       gotoPage() {
            vm.$router.push({path:'/'});
       },
       //
       gotoDetail(idx) {
           if(vm.orderList[idx].status<8){
                vm.$router.push({path:'/xiyi_detail',query:{'oId':vm.orderList[idx].id}})
           }
       },
       //
       cancel(idx){
            vm.receiveData.postData(vm,"/yunxiyi/bill/"+vm.orderList[idx].id+"/cancel",null,'res',function() {
                if(vm.res.success) {
                    vm.orderList[idx].status=8;
				    vm.orderList[idx].statusStr="";
                }else {
                    alert(vm.res.message?"":vm.res.message);
                }
            })
       },
       //
       pay(idx) {
            vm.receiveData.postData(vm,'/yunxiyi/pay/'+vm.orderList[idx].id,null,'res',function() {
                if(vm.res.success) {
                    wx.chooseWXPay({
                        "timestamp":vm.res.result.timestamp,
                        "nonceStr":vm.res.result.nonceStr,
                        "package":vm.res.result.pkgStr,
                        "signType":vm.res.result.signType,
                        "paySign":vm.res.result.signature,
                        success: function (res) {
                            vm.notifyPayed(idx);
                         }
                        });
                }else {
                      alert(vm.res.message?"":vm.res.message);
                }
            })
       },
       // 
       notifyPayed(idx) {
           let url ="/yunxiyi/notifyPayed/"+vm.orderList[idx].id;
                 vm.receiveData.getData(vm,url,'Data',function(){
                    vm.orderList[idx].statusStr="";
        		    vm.orderList[idx].status=2;  
                  });
       }
       
   },
   components: {},
   computed: {},
}
</script>

<style  scoped>
.xiyi_list {
    font-size: 1em;
    background-color: #f4f5f7;
}
.nopage {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #eeeeee;
    text-align: center;
}
.nopage_img {
    margin-top: 30%;
    width: 50%;
}
.nopage_desc {
    margin-top: 15px;
    font-size: 16px;
}
.btn_desc {
    position: fixed;
    bottom: 50px;
    font-size: 16px;
    width: 100%;
}
.order_btn {
    position: fixed;
    bottom: 0px;
    font-size: 22px;
    width: 100%;
    background-color: #ff8a00;
    color: white;
    padding: 8px;
}
.list_body{
    margin-bottom: 15px;
    box-sizing: border-box;
    clear: both;
    background-color: #fff;
    font-size:15px;
    border-bottom: 10px solid #f4f5f7;
}
.borderline{
    height:1px;
    background-image: url("../assets/images/index/line3.png");
}
.time_desc{
    color:#666666;
    padding:15px 0 0 15px;
}
.list_main{
    padding:15px;
    height:90px;
    clear:right;
}       
.item_logo{
    width:84px;
    height:84px;
    float:left;
    margin-right: 15px;
}
.item_desc{
    height:26px;
    line-height: 26px;
}
.highlight{
    color:#ff8a00;
}
.status_bar{
    height: 40px;
    line-height: 40px;
    padding: 0px 15px;
    border-top:1px solid #f4f5f7;
} 
.btns>span{
    display: inline-block;
    height: 28px;
    width:70px;
    line-height: 28px;
    text-align: center;
    border-radius: 5px;
    margin-left: 10px;
}
.cancel{
    border:1px solid #aaa;
}
.pay,.ems{
    background-color: #ff8a00;
    color:#fff;
}  
</style>


